<template>
  <div class="botupchat-profile-section-chat" :style="UIPrefs.chat_header.css">
    <div class="botupchat-chat-profile-pic" :style="UIPrefs.chat_header.css.profile_pic">
      <div class="botupchat-d-flex" style="display: flex; align-items:center;">
        <b-img v-if="!conversationHeader" :src="UIPrefs.chat_header.attr.gravatar" class="botupchat-profile" :style="UIPrefs.chat_header.css.img"></b-img>
        <div v-else @click="navigate" style="cursor:pointer;">
          <svg class="drift-default-icon drift-default-icon--back-arrow" width="18" height="18" viewBox="0 0 10 19">
            <path
              fill="#ffffff"
              d="M9.70711 0.542893C10.0976 0.933418 10.0976 1.56658 9.70711 1.95711L2.41421 9.25L9.70711 16.5429C10.0976 16.9334 10.0976 17.5666 9.70711 17.9571C9.31658 18.3476 8.68342 18.3476 8.29289 17.9571L0.292893 9.95711C0.105357 9.76957 -4.04995e-07 9.51522 -3.93402e-07 9.25C-3.81809e-07 8.98478 0.105357 8.73043 0.292893 8.54289L8.29289 0.542893C8.68342 0.152369 9.31658 0.152369 9.70711 0.542893Z"
              clip-rule="evenodd"
              fill-rule="evenodd"
            ></path>
          </svg>
        </div>
        <div class="botupchat-margin-left">
          <div v-if="!conversationHeader">
            <h4 :style="UIPrefs.chat_header.css.h4">{{ UIPrefs.chat_header.attr.bot_name }}</h4>
            <h5 :style="UIPrefs.chat_header.css.h5">{{ UIPrefs.chat_header.attr.bot_description }}</h5>
          </div>
          <h4 v-else :style="UIPrefs.chat_header.css.h4">{{ $t("botupChat.converstion_history") }}</h4>
        </div>
      </div>
      <div class="botupchat-contact-icons" v-if="!conversationHeader" :style="UIPrefs.chat_header.css.history_icon">
        <button class="botup-widget-history" aria-label="Open conversation history" @click="viewHistory()">
          <svg class="botup-default-icon--Time" width="16" height="16" viewBox="0 0 20 20" style="margin-top:6px;margin-left:1px;">
            <path
              d="M3.55,10.3l.79-.78a.77.77,0,0,1,1.09,1.09L3.3,12.74a.79.79,0,0,1-1.11,0l-2-2.13A.77.77,0,1,1,1.33,9.54l.69.73V10a9,9,0,1,1,3.07,6.77.77.77,0,1,1,1-1.16A7.45,7.45,0,1,0,3.56,10C3.56,10.08,3.56,10.18,3.55,10.3Zm7.72-4.93v4.87h3.34a.77.77,0,0,1,0,1.54H10.5A.76.76,0,0,1,9.73,11V5.37a.77.77,0,0,1,1.54,0Z"
              fill="#ffffff"
            ></path>
          </svg>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: "Header",
    components: {},
    props: {
      conversationHeader: Boolean,
    },
    data() {
      return {
        UIPrefs: window._Botup.UIPrefs.prefs,
      };
    },
    mounted() {},
    methods: {
      // Redirect to conversation details view
      viewHistory() {
        this.$router.push("/conversations");
      },

      // Navigate to active conversation view
      navigate() {
        this.$router.push({
          name: "chat",
          params: { active: true },
        });
      },
    },
  };
</script>



