<template>
  <div class="botupchat-footer-500app" :style="UIPrefs.chat_footer.css">
    <p class="botupchat-items-center mb-0" :style="UIPrefs.chat_footer.css.p_link" v-if="!conversationFooter">
      {{ $t("botupChat.footer_help_text") }}<b-link :style="UIPrefs.chat_footer.css.link">{{ $t("botupChat.500apps_text") }}</b-link>
    </p>
    <p class="botupchat-items-center mb-0 pt-1" v-else>
      {{ $t("botupChat.footer_help_text") }}<b-link :style="UIPrefs.chat_footer.css.link">{{ $t("botupChat.500apps_text") }}</b-link>
    </p>
  </div>
</template>
<script>
  export default {
    props: {
      conversationFooter: Boolean,
    },
    data() {
      return {
        UIPrefs: window._Botup.UIPrefs.prefs,
      };
    },
  };
</script>



